<template>
    <div class="commentlistsCon">
        <div class="commentHeader">评论</div>
        <div v-for="(item,index) in CommentList" :key='item.id'>
            <div class="commentlist">          
                <div class="headImg" :style="{backgroundImage:'url('+item.headimg+')'}"></div>
                <div class="listCon" @click='entry(item.commentId,item.userId)'>
                    <div class="name" v-text="item.nickname"></div>
                    <div class="text" v-text="item.comment"></div>
                    <div v-if='item.replyList'>
                        <div class="otherList" v-for="list in item.replyList" :key='list.name'>
                            <div class="name" v-text="`| ${list.nickname}`"></div>
                            <div class="text" v-text="list.comment"></div>
                        </div>
                    </div>
                    <div id='replyTips' v-if='item.replyNum>2'>查看全部{{item.replyNum}}条回复></div>
                </div>
            </div>
            <hr v-if='index!=CommentList.length-1'/>
        </div>
        <div class='CommentTips' @click='entry()' v-if='CommentList.length==0'>暂时没有评论,点击添加你的评论吧^_^</div>
    </div>
</template>
<script>
export default {
    name:'',
    props:['CommentList'],
    data () {
        return {

        }
    },
    methods:{
      entry(id,num=0) {
        if(event.target.id=='replyTips'){

        }else{
				  this.$router.push(`commentPage/${id||this.$route.params.contId}/${num}`);
        }
			},
    }
}
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
.commentlistsCon{
    border-bottom: 0.01rem solid rgb(229,229,229); 
    .commentHeader{
        width: 6.9rem;
        height: 0.96rem;
        line-height: 0.96rem;
        font-size: 0.32rem;
        text-align: center;
        color: #555555;
        margin-left: 0.33rem;
        border-bottom: 0.01rem solid rgb(229,229,229);
    }
    hr{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        height: 1px;
        background: #f2f2f2;
    }
    .commentlist{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        display: flex;
        .headImg{
            width: 0.72rem;
            height: 0.72rem;
            background-size: 0.72rem 0.72rem;
            background-repeat: no-repeat;
            border-radius: 0.72rem; 
            margin-right: 0.24rem
        }
        .listCon{
            overflow: hidden;
            flex: 1;
            .name{
                font-size: 0.28rem;
                color: #1797c6;
            }
            #replyTips{
                font-size: 0.28rem;
                color: #888888;
                margin-top: 0.11rem;
            }
            .text{
                word-break: break-all;
                word-wrap: break-word;
                line-height: 0.4rem;
                font-size: 0.32rem;
                color: #333333;
                // line-height: 0.58rem;
                margin: 0.11rem 0 0;
            }
            .otherList{
                margin: 0.11rem 0 0;
            }
        }
    }
    .CommentTips{
      padding:0.3rem 0;
      font-size: 0.2rem;
      text-align:center;
    }
}
</style>




