<template>
	<transition name="fade" mode="in-out">
		<div class="articleDetailsPage">
			<v-Header class="head">
				<span slot='text' class="headText" v-html='this.article.title'></span>
				<mt-button icon="more" class='more' @click='showMenu' slot="right"></mt-button>
			</v-Header>
			<div id='articleScrollCon'>
				<div>
					<div class="articleDetailsCon" v-if= 'this.article.title'>
						<!-- <div class="articleTagCon" v-html='`${this.article.nickname} ${this.article.publishDate}`'></div> -->
						<div class="articleTextCon">
							<img :src="this.article.preimg">
							<div class="articleTitleCon" v-html='this.article.title'></div>
							{{this.article.cont}}
						</div>
					</div>
					<div class="giveTheThumbsUp" v-if= 'this.article.like' @click="addPraise">
						<div class="addPraiseBtn"></div>{{this.article.like}}
					</div>
					<hr/>
					<v-CommentList :CommentList='CommentList' />
				</div>
			</div>
			<div class="commentInputCon">
				<input type="text" placeholder="快来说点什么吧！">
				<div class='inputBtn' @click="entry"></div>
				<div :class='["addPraiseBtn",this.article.like==2?"active2":"active1"]' @click="addPraise"></div>
			</div>
			<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
			<router-view/>
		</div>
	</transition>
</template>
<script>
	import Header from '../../components/header';
	import CommentList from '../articleDetails/commentList.vue';
	export default {
		name: '',
		components: {
			'v-Header': Header,
			'v-CommentList': CommentList
		},
		props: {},
		data() {
			return {
				actions: [{
					name: '编辑',
					method: this.updatePage
				}, {
					name: '下架',
					method: this.reclaim
				}],
				sheetVisible: false,
				isAddPraise: false,
				articleId:this.$route.params.contId,
				article:{},
				CommentList: [],
        lastCommentId:0,
        atUserId:this.$route.params.atUserId
			}
		},
		watch: {
      atUserId(v,o){
        if(v==undefined){
          this.CommentList=[];
          this.getComment();
        }
      }
    },
		computed: {},
		created() {this.getArticle();this.getComment();},
    updated() {this.atUserId=this.$route.params.atUserId;console.log(this.atUserId)},
		methods: {
      getComment(){
        this.$http.get(this.$port.COMMENT_LIST_PROT,{
          params:{
            contId:this.articleId,
            lastCommentId:this.lastCommentId
          }
        }).then((response) => {
          const {data} = response;
          if(data.code == 200){
            this.CommentList=[...this.CommentList,...data.data];
            console.log(data.data)
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
      },
			entry() {
				this.$router.push(`commentPage/${this.articleId}/0`);
			},
			showMenu() {
				this.sheetVisible = true;
			},
			addPraise(){
				console.log('点赞');
				if(this.article.like==1){
					this.$http.post(`${this.$port.ADDLIKE_PROT}?contId=${this.articleId}`,{
					}).then((response) => {
						const {data} = response;
						if(data.code == 200){
							this.getArticle();
						}
					})
					.catch((error) => {
						console.log(error.response.data);
					});
				}
			},
			updatePage() {
				console.log('编辑');
			},
			reclaim() {
				console.log('下架');
			},
			getArticle() {
        this.$http.get(this.$port.ARTICLE_PROT,{
          params:{
            productId:this.articleId
          }
        }).then((response) => {
          const {data} = response;
          if(data.code == 200){
            this.article={...this.article,...data.data};
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
			},
		}
	}
</script>
<style lang="scss" scoped>
	* {
		border: none;
		margin: 0;
		padding: 0;
	}

	.articleDetailsPage {
		width: 100%;
		height: auto;
		overflow: hidden;
		background: white;
		.head {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			.headText{
				display: inline-block;
				max-width: 5rem;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			.more {
				position: absolute;
				top: 0;
				right: 0;
				height: 0.8rem;
				width: 0.8rem;
				padding: 0;
				background: rgba(0, 0, 0, 0);
				box-shadow: none;
			}
		}
		#articleScrollCon {
			top: 0.82rem;
			left: 0;
			right: 0;
			bottom: 0.86rem;
			margin: auto;
			position: absolute;
			overflow-x: hidden;
			overflow-y: scroll;
			-webkit-overflow-scrolling: touch;
			background: white;
		}
		.articleDetailsCon {
			margin-top: 0.23rem;		
			.articleTagCon,
			.articleTextCon {
				width: 6.9rem;
				margin-left: 0.3rem;
			}
			.articleTitleCon {
				font-size: 0.44rem;
				font-weight: bold;
			}
			.articleTagCon {
				font-size: 0.22rem;
				color: #888888;
				// margin-top: 0.36rem;
			}
			.articleTextCon {
				font-size: 0.32rem;
				line-height: 0.62rem;
				// margin-top: 0.15rem;
				img {
					width: 100%;
					margin-top: 0.27rem;
				}
			}
		}
		.giveTheThumbsUp {
			width: 2.1rem;
			height: 0.72rem;
			margin: 0.6rem auto 0.32rem;
			background: #1797c6;
			border-radius: 0.72rem;
			font-size: 0.32rem;
			line-height: 0.72rem;
			text-align: center;
			color: white;
			display: flex;
			align-items: center;
			justify-content: center;
			.addPraiseBtn {
				margin-right: 0.08rem;
			}
		}
		hr {
			height: 0.2rem;
			background-color: #f2f2f2;
		}
		.commentInputCon {
			width: 100%;
			height: 0.86rem;
			position: fixed;
			bottom: 0;
			left: 0;
			background: #f5f5f5;
			input,
			.inputBtn {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0.16rem;
				margin: auto 0;
				width: 5.7rem;
				height: 0.6rem;
				line-height: 0.6rem;
				border-radius: 0.6rem;
				font-size: 0.3rem;
				text-indent: 1em;
				&::-webkit-input-placeholder {
					color: rgb(137, 137, 137);
					text-indent: 1em;
				}
			}
			.addPraiseBtn {
				position: absolute;
				bottom: 0.22rem;
				right: 0.62rem;
			}
		}
	}
</style>



